/*#
 * Side Cart
 */
.xoo-wsc-modal {

	.xoo-wsc-header,
	.xoo-wsc-body,
	.xoo-wsc-footer {
		@include prefix( transition, $trans_3, o moz webkit );
	}

	.xoo-wsc-footer {

		.xoo-wsc-tools {

			> div.xoo-wsc-tool {
				margin: auto;
			}
		}

		#xoo-wsc-coupon-code {
			border-radius: 100px 0 0 100px;

			&:focus {
				outline: 0;
				@include prefix( box-shadow, 0 1px 6px 0 rgba( #52abf6, .7 ), webkit moz o);
			}
		}

		.xoo-wsc-coupon-submit {
			border-radius: 0 100px 100px 0;
			text-transform: uppercase;
			display: flex;
			color: rgba( #fff, 1 );
			font-weight: 700;
			line-height: 1.1;
			letter-spacing: .1rem;
			text-align: center;
			border: 1px solid #52abf6;
			background: rgba( #52abf6, 1 );
			@include prefix( transition, $trans_25, webkit moz o);
		}

		.xoo-wsc-ft-btn {
			border-radius: 100px;
		}

		.xoo-wsc-rp-products {
			display: flex;
			align-items: center;

			.xoo-wsc-rp-item {
				align-items: center;
				justify-content: center;

				.xoo-wsc-rp-left-area {
					flex: 0 0 20%;
				}

				.xoo-wsc-rp-right-area {
					flex: 0 0 30%;

					span.price {
						display: flex;
					    align-items: center;
					    justify-content: flex-start;
					    width: 100%;
					    margin: 0;
					    color: #333333;
					    @include font-size( 1.1 );

					    ins {
					    	margin-left: 8px;
					    	color: #e81409;
					    	background: transparent;
					    }
					}
				}
			}
		}
	}
}

@media screen and ( max-width: 992px ) {
	.xoo-wsc-container {
		max-width: 100%;
	}
}