main.main-my-account {

  div#customer_login {

    div.input-group {

      input.input-text.is-invalid {
        @include prefix( box-shadow, 0px 0px 5px rgba(#e2401c, .6 ), o moz webkit );
      }
    }
  }
}

.woocommerce-MyAccount-navigation {

  .wonka-btn {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.woocommerce-password-strength {
  clear: both;
  width: 100%;
  text-align: left;
}

body.woocommerce-lost-password {
  
  div.woocommerce-notices-wrapper {
    display: none;
  }
}